@font-face {
    font-family: "icons-turn-arrow";
    src: url("./fonts/icons/turn-arrow.eot") format("embedded-opentype"),
        url("./fonts/icons/turn-arrow.woff") format("woff"),
        url("./fonts/icons/turn-arrow.ttf") format("truetype"),
        url("./fonts/icons/turn-arrow.svg") format("svg");
}

html, body {
  width: 100%;
  height: 100%;

  background-color: #222;
}

.root {
  width: 100%;
  height: 100%;
}

/* stage -- start */
.stage {
  position: relative;

  width: 100%;
  height: 680px;
}
/* stage -- end */

/* image -- start */
.image-sec {
  position: relative;

  width: 100%;
  height: 100%;
  overflow: hidden;

  background-color: #ddd;

  -webkit-perspective: 1800px;

          perspective: 1800px;
}

.img-figure {
  position: absolute;

  width: 320px;
  height: 360px;
  margin:0px;
  padding: 40px;

  background-color: #fff;
  
  -webkit-box-sizing: border-box;
  
          box-sizing: border-box;
  cursor: pointer;

  -webkit-transform-origin: 0 50% 0;

      -ms-transform-origin: 0 50% 0;

          transform-origin: 0 50% 0;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
  transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
  -o-transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out;
  transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out;
  transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
  
}

.img-figure.is-inverse {
    -webkit-transform: translate(320px) rotateY(180deg);
            transform: translate(320px) rotateY(180deg);
}

figcaption {
  text-align:center;
}

.img-title {
  margin: 20px 0 0 0;

  color: #a7a0a2;

  font-size: 16px;
}

.img-back {
    position: absolute;
    left: 0px;
    top: 0px;

    width: 100%;
    height: 100%;
    padding: 50px 40px;
    overflow: auto;

    color: #a7a0a2;
    font-size: 22px;
    line-height: 1.25;
    text-align: left;

    background: #fff;

    -webkit-box-sizing: border-box;

            box-sizing: border-box;
    -webkit-transform: rotateY(180deg) translateZ(1px);
            transform: rotateY(180deg) translateZ(1px);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    
}
/* image -- end*/

/* controller -- start */
.controller-nav {
  position: absolute;
  left: 0px;
  bottom: 30px;
  z-index: 101;

  width: 100%;

  text-align: center;
}

.controller-nav .controller-unit {
    display: inline-block;
    margin: 5px;
    width: 30px;
    height: 30px;

    text-align: center;
    vertical-align: middle;

    cursor: pointer;
    background-color: #aaa;
    border-radius: 50%;
    -webkit-transform: scale(0.5);
        -ms-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-transition: background-color .3s, -webkit-transform .6s ease-in-out;
    transition: background-color .3s, -webkit-transform .6s ease-in-out;
    -o-transition: transform .6s ease-in-out, background-color .3s;
    transition: transform .6s ease-in-out, background-color .3s;
    transition: transform .6s ease-in-out, background-color .3s, -webkit-transform .6s ease-in-out;
}

.controller-nav .controller-unit.is-center {
    background-color: #888;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
}

.controller-nav .controller-unit.is-center::after {
    font-family: "icons-turn-arrow";
    content: "\e600";
    color: #fff;
    line-height: 30px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smothing: greyscale;
    font-size: 80%;
}

.controller-nav .controller-unit.is-center.is-inverse {
    background-color: #555;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
/* controller -- end */




